body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@font-face {
    font-family: "EllenFont";
    src: url(/static/media/myfont-regular-webfont.f121731b.eot);
    src: url(/static/media/myfont-regular-webfont.d60af048.woff) format("woff"),
        url(/static/media/Myfont-Regular.b9c25239.otf) format("opentype"),
        url(/static/media/myfont-regular-webfont.1f26879a.svg) format("svg");
}

#header {
    margin: 0px;
    margin-bottom: 0px;
}

#intro {
    height: 88vh;
    background-color: #9fe0dc;
    margin-top: 0px;
    position: relative;
}

#headshot {
    height: 90%;
    display: block;
    position: absolute;
    bottom: 0;
    left: 22%;
}

#caption {
    font-family: "EllenFont";
    position: absolute;
    right: 13%;
    top: 40%;
    padding: 1.5em;
    background-color: white;
    border-radius: 5px;
    margin: auto;
    box-shadow: 5px 10px#02C8A7;
    max-width: 30%;
}

#caption h3 {
    font-size: 7vh;
}

#caption h5 {
    font-size: 4vh;
}

@media only screen and (max-width: 960px) {
    #headshot {
        position: absolute;
        bottom: 0;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }

    #caption {
        position: absolute;
        min-width: 80%;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        top: 70%;
    }
}

@media only screen and (max-width: 480px) {
    #caption h3 {
        font-size: 200%;
    }

    #caption h5 {
        font-size: 150%;
    }
}

#about {
    background-color: #eaf2f8;
    padding: 5% 0;
}

#about-title {
    font-family: "Space Mono", monospace;
    color: black;
    padding: 10px 30px;
    margin: 0;
    display: block;
}

@media only screen and (min-width: 960px) {
    #about-title {
        margin: 0 0 0 10%;
    }
}

#about-icon {
    height: 250px;
    display: block;
    margin: 30px;
    padding: 5px;
    border: 2px solid #b5bdc3;
    border-radius: 50%;

    -webkit-transition: -webkit-filter 0.5s ease-in-out;

    transition: -webkit-filter 0.5s ease-in-out;

    transition: filter 0.5s ease-in-out;

    transition: filter 0.5s ease-in-out, -webkit-filter 0.5s ease-in-out;
    -webkit-filter: grayscale(50%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(50%);
}

#about-icon:hover {
    -webkit-filter: grayscale(0%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(0%); /* FF 35+ */
}

#about-content {
    margin: auto;
    width: 80%;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    border: 2px solid #b5bdc3;
}

#experience {
    background-color: #283747;
    height: 400px;
}
#projects {
    font-family: "Space Mono", monospace;
    background-color: #ffffff;
    /* min-height: 520px; */
    padding: 20px;
}

#projects-title {
    font-family: "Space Mono", monospace;
    /* color: #1c2833; */
    color: black;
    background-color: white;
    padding: 10px 30px;
    margin: 0;
}

@media only screen and (min-width: 960px) {
    #projects-title {
        margin: 0 0 0 10%;
    }
}

#contact {
    /* background-color: #1a5276; */
    background-color: #f9be02;
    height: 500px;
}

#contact-title {
    font-family: "Space Mono", monospace;
    color: black;
    padding: 10px 30px;
    margin: 0;
}

#footer {
    background-color: rgb(34, 34, 34);
    color: white;
    padding: 5px;
}

